.specialists-section {
  text-align: center;
  padding: 80px 40px;
  background: #ffffff;
}

.specialists-header .tag {
  background: #f1f3ff;
  color: #3b3b98;
  padding: 6px 18px;
  border-radius: 20px;
  font-size: 13px;
  font-weight: 600;
  display: inline-block;
}

.specialists-header h2 {
  font-size: 50px;
  font-weight: 500;
  color: #1b1b3a;
  line-height: 1.2;
  margin-bottom: 50px;
}

.specialists-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 40px;
  justify-content: center;
}

.specialist-card {
  background: #ffffff;
  border-radius: 20px;
  padding: 25px;
  box-shadow: 0px 25px 60px rgba(0,0,0,0.08);
  transition: transform 0.3s ease;
}

.specialist-card:hover {
  transform: translateY(-10px);
}

.specialist-img img {
  width: 100%;
  border-radius: 16px;
}

.specialist-card h3 {
  margin-top: 18px;
  font-size: 20px;
  color: #1b1b3a;
  font-weight: 600;
}

.specialist-card p {
  margin-top: 6px;
  color: #777;
  font-size: 16px;
}

@media (max-width: 991px) {
  .specialists-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 576px) {
  .specialists-grid {
    grid-template-columns: repeat(1, 1fr);
  }
}

@media (max-width: 768px) {
  .services-section {
    padding: 50px 20px;
  }
  .service-img {
    height: 150px;
  }
}